export default {
	data() {
		return {
			list: null
		};
	},
	onLoad() {
		uni.request({
			url: this.$store.state.baseUrl + '/calculation/index/get_order',
			method: 'POST',
			data: {
				token: uni.getStorageSync('token')
			},
			header: {
				"Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
			},
			success: (res) => {
				if (res.data.code == 1) {
					this.list = res.data.data
				} else {
					uni.showToast({
						'title': res.data.msg,
						'icon': 'none'
					})
				}

			}
		})
	}
}
